import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { searchCoin } from "../actions/coinActions";
import { Navbar, Nav, Form, FormControl, Button } from "react-bootstrap";

const NavBar = () => {
  const [keyword, setKeyword] = useState("");
  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();
    console.log(keyword);
    let formattedKeyword = keyword.toLowerCase();
    dispatch(searchCoin(formattedKeyword));
  };
  return (
    <Navbar
      bg="primary"
      variant="dark"
      style={{
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
        overflow: "hidden",
      }}>
      <Navbar.Brand href="/">Crypto Tracks</Navbar.Brand>
      <Nav className="mr-auto">
        <Nav.Link base href="/">
          Home
        </Nav.Link>
        <Nav.Link href="https://github.com/JakeScudder">
          <i class="fab fa-github"></i>
        </Nav.Link>
        <Nav.Link href="#">
          <i class="fab fa-instagram"></i>
        </Nav.Link>
      </Nav>
      <Form onSubmit={submitHandler} inline>
        <FormControl
          type="text"
          placeholder="Search"
          className="mr-sm-2 search-input"
          onChange={(e) => setKeyword(e.target.value)}
        />
        <Button type="submit" variant="outline-light">
          Search
        </Button>
      </Form>
    </Navbar>
  );
};

export default NavBar;
